import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { ValidateService } from '../../services/validate.service';
import { Router } from '@angular/router';

@Component({
    selector: 'app-profile',
    templateUrl: './profile.component.html',
    styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
    user: any;

    editingName: Boolean;
    editingUsername: Boolean;
    editingEmail: Boolean;
    editingPassword: Boolean;

    newName: String;
    newUsername: String;
    newEmail: String;
    newPassword: String;
    newPasswordAgain: String;

    errorName: String;
    errorUsername: String;
    errorEmail: String;
    errorPassword: String;

    deletePrompt: Boolean = false;

    constructor(
        private validateService: ValidateService,
        private authService: AuthService,
        private router: Router
    ) { }

    ngOnInit() {
        this.authService.getProfile().subscribe(profile => {
            this.user = profile.user;
            console.log(profile);
        }, err => {
            console.log(err);
            return false;
        });
    }

    /////////////////////////////////////////////////////////////////

    updateName() {
        if (!this.newName) {
            this.errorName = "name can't be empty";
            return false;
        }
        let updatedUser = {
            _id: this.user._id,
            name: {
                new: true,
                val: this.newName
            },
            username: {
                new: false
            },
            email: {
                new: false
            },
            password: {
                new: false
            }
        };
        this.authService.updateUser(updatedUser).subscribe(data => {
            console.log('update name success');
            console.log(data);
            this.editingName = false;
            this.newName = '';
            this.errorName = '';
            this.user = data;
        }, err => {
            this.errorName = err.message;
            console.log(err);
        });
    }

    updateUsername() {
        if (!this.newUsername) {
            this.errorUsername = "username can't be empty";
            return false;
        }
        let updatedUser = {
            _id: this.user._id,
            name: {
                new: false
            },
            username: {
                new: true,
                val: this.newUsername
            },
            email: {
                new: false
            },
            password: {
                new: false
            }
        };
        this.authService.updateUser(updatedUser).subscribe(data => {
            this.editingUsername = false;
            this.newUsername = '';
            this.errorUsername = '';
            this.user = data;
        }, err => {
            console.log(err);
            this.errorUsername = err.message;
        });
    }

    updateEmail() {
        if (!this.validateService.validateEmail(this.newEmail)) {
            this.errorEmail = "email is invalid";
            return false;
        }
        let updatedUser = {
            _id: this.user._id,
            name: {
                new: false
            },
            username: {
                new: false
            },
            email: {
                new: true,
                val: this.newEmail
            },
            password: {
                new: false
            }
        };
        this.authService.updateUser(updatedUser).subscribe(data => {
            console.log(data);
            this.editingEmail = false;
            this.newEmail = '';
            this.errorEmail = '';
            this.user = data;
        }, err => {
            console.log(err);
            this.errorEmail = err.message;
        });
    }

    updatePassword() {
        if (!this.newPassword || !this.newPasswordAgain) {
            this.errorPassword = "password can't be empty";
            return false;
        }
        if (this.newPassword != this.newPasswordAgain) {
            this.errorPassword = "passwords must match";
            return false;
        }
        let updatedUser = {
            _id: this.user._id,
            name: {
                new: false
            },
            username: {
                new: false
            },
            email: {
                new: false
            },
            password: {
                new: true,
                val: this.newPassword
            }
        };
        this.authService.updateUser(updatedUser).subscribe(data => {
            console.log('update password success');
            console.log(data);
            this.editingPassword = false;
            this.newPassword = '';
            this.newPasswordAgain = '';
            this.errorPassword = '';
            this.user = data;
        }, err => {
            this.errorPassword = err.message;
            console.log(err);
        });
    }

    /////////////////////////////////////////////////////////////////

    cancelEditName(){
        this.editingName = false;
        this.errorName = '';
        this.newName = '';
    }

    cancelEditUsername(){
        this.editingUsername = false;
        this.errorUsername = '';
        this.newUsername = '';
    }

    cancelEditEmail(){
        this.editingEmail = false;
        this.errorEmail = '';
        this.newEmail = '';
    }

    cancelEditPassword(){
        this.editingPassword = false;
        this.errorPassword = '';
        this.newPassword = '';
        this.newPasswordAgain = '';
    }

    /////////////////////////////////////////////////////////////////

    deleteConfirm() {
        this.authService.deleteUser(this.user._id).subscribe(data => {
            console.log('delete success');
            console.log(data);
            this.authService.logout();
            this.router.navigate(['/']);
        }, err => {
            console.log('delete error');
            console.log(err);
        });
    }

}



