import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { tokenNotExpired } from 'angular2-jwt';
import 'rxjs/add/operator/map';

@Injectable()
export class AuthService {
    authToken: any;
    user: any;

    constructor(
        private http: Http
    ) { }

    // for each of the http requests
    // local testing should be at " http://localhost:3000/ "
    // deployment to heroku should be empty

    registerUser(user) {
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        return this.http.post('users/register', user, { headers: headers }).map(res => res.json());
    }

    authenticateUser(user) {
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        return this.http.post('users/authenticate', user, { headers: headers }).map(res => res.json());
    }

    getProfile() {
        let headers = new Headers();
        this.loadUserData();
        headers.append('Authorization', this.authToken);
        headers.append('Content-Type', 'application/json');
        return this.http.get('users/profile', { headers: headers }).map(res => res.json());
    }

    deleteUser(id) {
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        return this.http.delete('users/delete?id=' + id, { headers: headers }).map(res => res);
    }

    updateUser(user) {
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        return this.http.put('users/update', user, { headers: headers }).map(res => res.json());
    }

    getMessages(oldestId) {
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        return this.http.get('messages/home?id=' + oldestId, { headers: headers }).map(res => res.json());
    }

    ///////////////////////////////////////////////////////////////////////

    loadUserData() {
        const token = localStorage.getItem('id_token');
        const user = JSON.parse(localStorage.getItem('user'));
        this.authToken = token;
        this.user = user;
        //console.log(user);
    }

    storeUserData(token, user) {
        localStorage.setItem('id_token', token);
        localStorage.setItem('user', JSON.stringify(user));
        this.authToken = token;
        this.user = user;
    }

    getUser() {
        return this.user;
    }

    loggedIn() {
        return tokenNotExpired('id_token');
    }

    logout() {
        this.authToken = null;
        this.user = null;
        localStorage.clear();
    }
}



