import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { WebsocketService } from '../../services/websocket.service';
import { element } from 'protractor';

@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.css'],

})
export class DashboardComponent implements OnInit {
    user: any;
    socket: any;

    messageOldestId: String = '';

    usersRecent: String[] = [];
    usersOnline: String[] = [];
    messages: any[] = [];

    messageModel: String;
    messageEdit: String;

    moreMessages: Boolean;
    loadingMessages: Boolean;

    constructor(
        private authService: AuthService,
        private websocketService: WebsocketService
    ) { }

    ngOnInit() {
        this.authService.getProfile().subscribe(profile => {
            this.user = profile.user;
            this.websocketService.socket.emit('new user', this.user._id, function (data) { });

            //////////////////////////////////////////////////////////
            this.loadingMessages = true;
            this.authService.getMessages('').subscribe(messages => {
                for (let i = 0; i < messages.length; i++) {
                    this.messages.unshift(messages[i]);
                }
                if (messages[0]) {
                    this.messageOldestId = this.messages[0]._id;
                }
                let element = document.getElementById("messages-container");
                setTimeout(function () {
                    element.scrollTop = element.scrollHeight - element.clientHeight;
                }, 100);

                this.moreMessages = true;
                if (messages.length < 20) {
                    this.moreMessages = false;
                }
                this.loadingMessages = false;
            }, error => {
                console.log('no messages');
                //return false;
            });
            //////////////////////////////////////////////////////////

        }, error => {
            console.log('no user');
            //return false;
        });

        this.websocketService.socket.on('new message', (data) => {
            this.messages.push(data);
            let element = document.getElementById("messages-container");
            setTimeout(function () {
                element.scrollTop = element.scrollHeight - element.clientHeight;
            }, 100);
        });

        this.websocketService.socket.on('new users', (data) => {
            this.usersOnline.length = 0;
            this.usersOnline.length = data.length;
            for (let i = 0; i < data.length; i++) {
                this.usersOnline[i] = data[i];
            }
        });

    }

    messageKeyDown(messageEvent) {
        if (messageEvent.key == 'Enter') {
            this.sendMessage();
        }
    }

    messageKeyUp(messageEvent) {
        if (messageEvent.key == 'Enter') {
            this.messageModel = '';
        }
    }

    sendMessage() {
        this.messageEdit = this.messageModel;
        this.messageModel = '';
        if (this.messageEdit[this.messageEdit.length - 1] == '\n') {
            this.messageEdit = this.messageEdit.substring(0, this.messageEdit.length - 1);
        }
        if (this.messageEdit) {
            this.websocketService.socket.emit('new message', this.messageEdit);
        }
        document.getElementById("entermessage-textarea").focus();
    }

    getMoreMessages() {
        let element = document.getElementById("messages-container");
        let scrollHeightPrev = element.scrollHeight;
        let scrollTopPrev = element.scrollTop;
        this.loadingMessages = true;
        this.authService.getMessages(this.messageOldestId).subscribe(messages => {
            for (let i = 0; i < messages.length; i++) {
                this.messages.unshift(messages[i]);
            }
            if (messages[0]) {
                this.messageOldestId = this.messages[0]._id;
            }
            setTimeout(function () {
                element.scrollTop = scrollTopPrev + (element.scrollHeight - scrollHeightPrev);
            }, 100);

            this.moreMessages = true;
            if (messages.length < 20) {
                this.moreMessages = false;
            }

            this.loadingMessages = false;
        }, error => {
            console.log('no more messages');
            //return false;
        });
    }
}



