import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
    eoru: String;
    password: String;

    constructor(
        private authService: AuthService,
        private router: Router,
        private flashMessagesService: FlashMessagesService
    ) { }

    ngOnInit() {
    }

    onLoginSubmit() {
        const user = {
            email: this.eoru,
            username: this.eoru,
            password: this.password
        }

        if (!user.email || !user.username || !user.password) {
            this.flashMessagesService.show('Please fill in all fields', { cssClass: 'alert-danger', time: 3000 });
            return false;
        }

        this.authService.authenticateUser(user).subscribe((data) => {
            if(data.success){
                this.flashMessagesService.show(data.msg, { cssClass: 'alert-success', time: 3000 });
                this.authService.storeUserData(data.token, data.user);
                this.router.navigate(['/chat']);
            }else{
                this.flashMessagesService.show(data.msg, { cssClass: 'alert-danger', time: 3000 });
            }
        });
    }
}



