import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
    selector: 'app-register',
    templateUrl: './register.component.html',
    styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
    name: String;
    username: String;
    email: String;
    password: String;
    passwordagain: String;

    constructor(
        private validateService: ValidateService,
        private authService: AuthService,
        private flashMessagesService: FlashMessagesService,
        private router: Router
    ) { }

    ngOnInit() {
    }

    onRegisterSubmit() {

        const user = {
            name: this.name,
            username: this.username,
            email: this.email,
            password: this.password,
        }      

        if (!this.validateService.validateRegister(user)) {
            this.flashMessagesService.show('Please fill in all fields', { cssClass: 'alert-danger', time: 3000 });
            return false;
        }

        if(!this.validateService.validatePasswords(user.password, this.passwordagain)){
            this.flashMessagesService.show('Passwords must match', { cssClass: 'alert-danger', time: 3000 });
            return false;
        }

        if (!this.validateService.validateEmail(user.email)) {
            this.flashMessagesService.show('Please use a valid email', { cssClass: 'alert-danger', time: 3000 });
            return false;
        }

        this.authService.registerUser(user).subscribe((data) => {
            console.log(data);
            if (data.success) {
                this.flashMessagesService.show(data.msg, { cssClass: 'alert-success', time: 3000 });
                this.router.navigate(['/login']);
            } else {
                this.flashMessagesService.show(data.msg, { cssClass: 'alert-danger', time: 3000 });
            }

        });

    }

}



